.pagination__list {
margin-top: 36px;
}

.pagination__list ul {
display: flex;
justify-content: center;
margin-left: auto;
margin-right: auto;
align-items: center;
width: 100%;
height: 100%;
max-width: 272px;
max-height: 32px;
list-style-type: none;
gap: 8px;
}

.pagination__list-btn button {
padding: 6px 4px;
width: 32px;
height: 32px;
background: #ffffff;
border-radius: 4px;
font-style: normal;
font-weight: 700;
font-size: 14px;
line-height: 20px;
text-align: center;
cursor: pointer;
}

.pagination__list-btn:first-child button {
background: #919eab;
}

.pagination__list-btn:first-child img:hover {
filter: invert(10%) sepia(99%) saturate(7274%) hue-rotate(261deg)
brightness(90%) contrast(129%);
}

.pagination__list-btn button:hover{
color: #BC3CD8;
border: 1px solid #BC3CD8;
}

.pagination__list-btn img:hover {
filter: invert(10%) sepia(99%) saturate(7274%) hue-rotate(261deg)
brightness(90%) contrast(129%);
}

.active{
color: #BC3CD8;
border-radius: 6px;
border: 1px solid #BC3CD8;
}

