        line-height: 27px;
    }

    .users__filter__search {
        display: flex;
        justify-content: space-between;
    }

    .users__filter__search select {
        padding-left: 20px;
        width: 145px;
        height: 36px;
        font-weight: 500;
        font-size: 14px;
        line-height: 21px;
    }

    .users__filter__search-box option {
        width: 145px;
    }

    .users__filter__logo-mobile h3 {
        max-width: 115px;
        line-height: 18px;
    }

    /* users datas */

    .users__datas {
        align-items: flex-start;
        margin-left: auto;
        margin-right: auto;
        width: 100%;
        max-width: 666px;
    }

    .users__datas-wrap {
        width: 100%;
        min-width: 366px;
        overflow-x: auto;
    }

    .users__datas-table {
        width: 254%;
    }

    ::-webkit-scrollbar {
        width: 4px;
        height: 8px;
        background: #c993d4;
        border-radius: 4px;
    }

    ::-webkit-scrollbar-thumb {
        border-radius: 4px;
        background: rgba(188, 60, 216, 1);
    }
}

